.main-side-bar-component{
    background: var(--White, #FFF);
    padding: 15px 16px;
    height: 100vh;
    min-width: 300px;
}
.left-logo-div{
    cursor: pointer;
}
.navigation-text{
    color: var(--Gray-700, #414651);
    font-family: "Noto Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
    margin-bottom: 0px;
    transition: all 0.3s ease-in-out;
}
.name-text{
    color: var(--Gray-900, #181D27);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px; /* 142.857% */
    margin: 0px;
}
.uan-number{
    color: var(--Gray-600, #535862);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
    margin: 0px;
}
.epfo-text{
color: #000;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 600;
line-height: 22.393px; /* 139.957% */
margin: 0px;
}
.nav-option{
    padding: 8px 12px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.nav-option:hover{
border-radius: 4px;
background: linear-gradient(130deg, rgba(73, 47, 178, 0.12) -1.74%, rgba(73, 47, 178, 0.07) 125.81%);
    .navigation-text{
        background: var(--1, linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%));
background-clip: text;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
        font-weight: 600;
    }
    .nav-svg path{
        fill: #492FB2;
    }
    .dash path{
        stroke: #492FB2;
    }
}
.active{
border-radius: 4px;
background: linear-gradient(130deg, rgba(73, 47, 178, 0.12) -1.74%, rgba(73, 47, 178, 0.07) 125.81%);
    .navigation-text{
        background: var(--1, linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%));
background-clip: text;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
        font-weight: 600;
    }
    .nav-svg path{
        fill: #492FB2;
    }
        .dash path{
        stroke: #492FB2;
    }
}


/* Arrow rotation */
.arrow {
  transition: transform 0.3s ease;
  cursor: pointer;
}
.arrow.open {
  transform: rotate(180deg);
}

/* Submenu smooth open/close */
.submenu {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, opacity 0.3s ease;
  opacity: 0;
  padding-left: 1rem; /* optional indentation */
}
.submenu.open {
  max-height: 500px; /* big enough to fit items */
  opacity: 1;
}
